<template>
  <div id="app">
    <select v-model="script">
      <option value="NIFTY">NIFTY</option>
      <option value="BANKNIFTY">BANKNIFTY</option>
    </select>
    <span v-on:click="autoFetch" v-if="fetchingStarted==false">Start</span>
    <span v-on:click="clearFetch" v-if="fetchingStarted==true">Stop</span>
<input type="text" v-model="interval" />

    <ul>
      <li v-for="k in history" :key="k" v-on:click="applyStoredData(k)" style="cursor:pointer;">
        {{ k }} <span v-on:click="removeStoredItems(k)" style="cursor:pointer; color:#f00;">Remove</span>
      </li>
    </ul>

    <!-- <button v-on:click="addLabel">add {{ type }}</button>&nbsp; -->
    <button v-on:click="storeCurrentData">save {{ type }}</button>

    <!-- <HelloWorld msg="Update chart dynamically"/> -->
    <visualisation
      type="openInterest"
      interval="1000"
      :script="script"
      :chart_data="chart_data"
    ></visualisation>
    <visualisation
      type="changeinOpenInterest"
      interval="1000"
      :script="script"
      :chart_data="chart_data"
    ></visualisation>
    <visualisation
      type="totalTradedVolume"
      interval="1000"
      :script="script"
      :chart_data="chart_data"
    ></visualisation>
    <visualisation
      type="pChange"
      interval="1000"
      :script="script"
      :chart_data="chart_data"
    ></visualisation>
    <visualisation
      type="bidprice"
      interval="1000"
      :script="script"
      :chart_data="chart_data"
    ></visualisation>
    <visualisation
      type="askPrice"
      interval="1000"
      :script="script"
      :chart_data="chart_data"
    ></visualisation>
    <visualisation
      type="pchangeinOpenInterest"
      interval="1000"
      :script="script"
      :chart_data="chart_data"
    ></visualisation>
    <visualisation
      type="impliedVolatility"
      interval="1000"
      :script="script"
      :chart_data="chart_data"
    ></visualisation>
    <!-- <visualisation type="identifier"></visualisation> -->
  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'
import Visualisation from "./components/Visualisation.vue";

export default {
  name: "App",
  components: {
    // HelloWorld,
    Visualisation,
  },
  data() {
    return {
      script: "NIFTY",
      chart_data: null,
      chart_data_nifty: null,
      chart_data_banknifty: null,
      interval: 5000,
      intervalTimeout: null,
      fetchingStarted: false,
      history: [],
      data_key: '',
      type: '',
    };
  },
  mounted: function () {
    let c_script = localStorage.getItem("script");
    if (c_script) {
      this.script = c_script;
    } else {
      localStorage.setItem("script", "NIFTY");
    }

    this.data_key = this.script + "_data_";
  },
  created: function () {
    this.renderCharts();
  },
  methods: {
    getStoredItems() {
      const items = { ...localStorage };
      this.history = Object.keys(items).filter(function(o){
        return o.indexOf('saved') > -1 ? o : false;
      });
    },
    removeStoredItems(key) {
      localStorage.removeItem(key);
      this.getStoredItems();
    },
    applyStoredData(k) {
      if(k.indexOf('_NIFTY_') > -1){
        this.script = 'NIFTY';
      }else{
        this.script = 'BANKNIFTY';

      }
      var data = this.getWithExpiry(k);
      if (data) {
        this.chart_data = JSON.parse(data);
        // this.applyData(JSON.parse(data), this);
        clearInterval(this.intervalTimeout);
      }
    },
    storeCurrentData() {
      var data = this.chart_data;
      var d = new Date();
      var time =
        d.getDate() +
        "_" +
        d.getHours() +
        "_" +
        d.getMinutes() +
        "_" +
        d.getSeconds();
      if (data) {
        this.setWithExpiry(
          "saved_" + this.data_key + time,
          JSON.stringify(data),
          60 * 60 * 60 * 60
        );
      }
      this.getStoredItems();
    },
    setWithExpiry(key, value, ttl) {
      const now = new Date();

      // `item` is an object which contains the original value
      // as well as the time when it's supposed to expire
      const item = {
        value: value,
        expiry: now.getTime() + ttl,
      };
      localStorage.setItem(key, JSON.stringify(item));
    },
    getWithExpiry(key) {
      const itemStr = localStorage.getItem(key);
      // if the item doesn't exist, return null
      if (!itemStr) {
        return null;
      }
      const item = JSON.parse(itemStr);
      const now = new Date();
      // compare the expiry time of the item with the current time
      if (now.getTime() > item.expiry) {
        // If the item is expired, delete the item from storage
        // and return null
        localStorage.removeItem(key);
        return null;
      }
      return item.value;
    },
    autoFetch(){
      this.fetchingStarted = true;
      this.intervalTimeout = setInterval(this.renderCharts,this.interval);
    },
    clearFetch(){
      this.fetchingStarted = false;
      clearTimeout(this.intervalTimeout);
    },
    renderCharts() {
      
      this.getStoredItems();
      if (this.script == "NIFTY") {
        if (this.chart_data_nifty != null) {
          this.chart_data = this.chart_data_nifty;
        }
      } else {
        if (this.chart_data_banknifty != null) {
          this.chart_data = this.chart_data_banknifty;
        }
      }
      //   let c_script = localStorage.getItem("script");
      // if (c_script) {
      //   this.script = c_script;
      // } else {
      //   localStorage.setItem("script", "NIFTY");
      // }

      var url = "";
      //  url = 'http://localhost:8080/option-chain-indices.json?'+this.script;
      url = "https://www.nseindia.com/api/option-chain-indices?symbol=" +
        this.script;
      var th = this;

      fetch(url, {
        credentials: "omit",
        headers: {
          mode: "no-cors", // 'cors' by default
          "Access-Control-Allow-Origin": "*",
          Origin: "nseindia.com",
        },
      })
        .then((r) => {
          if (r.status == 200) {
            return r.json();
          } else {
            return false;
          }
        })
        .then(function (r) {
          if (r) {
            th.chart_data = r;
            if (th.script == "NIFTY") {
              th.chart_data_nifty = r;
            } else {
              th.chart_data_banknifty = r;
            }
            // th.setWithExpiry(th.data_key, JSON.stringify(r), 30000);

            var d = new Date();
            th.last_updated =
              d.getDate() +
              "-" +
              d.getHours() +
              ":" +
              d.getMinutes() +
              ":" +
              d.getSeconds();

            // requesting = false;
          }
        });
    },
  },
  watch: {
    // data: function () {
    //   this._chart.destroy();
    //   this.renderChart(this.data, this.options);
    //   // this.fillData();
    // },
    // script: function () {
    //   this.data_key = this.script + "_data_";
    //   localStorage.setItem("script", this.script);
    //   // this._chart.destroy();
    //   //this.renderChart(this.data, this.options);
    //   // this.fillData();
    // },
    script: function () {
      this.data_key = this.script + "_data_";

      localStorage.setItem("script", this.script);
      this.renderCharts();
      // this.getStoredItems();
    },
  },
};
</script>

